
import type { Question, Difficulty, TestType, TestSettings } from '@/types';

const TEST_CONFIG: Record<TestType, Record<Difficulty, TestSettings>> = {
  'addition-subtraction': {
    easy: { numQuestions: 20, timeLimit: 300, title: 'Addition & Subtraction (Easy)', icon: 'brain-circuit' },
    medium: { numQuestions: 40, timeLimit: 600, title: 'Addition & Subtraction (Medium)', icon: 'brain-circuit' },
    hard: { numQuestions: 60, timeLimit: 900, title: 'Addition & Subtraction (Hard)', icon: 'brain-circuit' },
  },
  multiplication: {
    easy: { numQuestions: 20, timeLimit: 300, title: 'Multiplication (Easy)', icon: 'x' },
    medium: { numQuestions: 40, timeLimit: 600, title: 'Multiplication (Medium)', icon: 'x' },
    hard: { numQuestions: 60, timeLimit: 900, title: 'Multiplication (Hard)', icon: 'x' },
  },
  division: {
    easy: { numQuestions: 20, timeLimit: 300, title: 'Division (Easy)', icon: 'divide' },
    medium: { numQuestions: 40, timeLimit: 600, title: 'Division (Medium)', icon: 'divide' },
    hard: { numQuestions: 60, timeLimit: 900, title: 'Division (Hard)', icon: 'divide' },
  },
};

export function getTestSettings(testId: TestType, difficulty: Difficulty): TestSettings | undefined {
  return TEST_CONFIG[testId]?.[difficulty];
}

function getRandomInt(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

function shuffleArray<T>(array: T[]): T[] {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
}

function generateOptions(correctAnswer: number): number[] {
  const options = new Set<number>([correctAnswer]);
  const range = Math.max(10, Math.abs(Math.floor(correctAnswer * 0.2)));

  while (options.size < 4) {
    const minOption = Math.max(0, correctAnswer - range);
    const maxOption = correctAnswer + range;
    const wrongAnswer = getRandomInt(minOption, maxOption);
    
    if (wrongAnswer !== correctAnswer && wrongAnswer >= 0) {
      options.add(wrongAnswer);
    }
  }
  return shuffleArray(Array.from(options));
}

function getNumberRange(difficulty: Difficulty): [number, number] {
    switch (difficulty) {
        case 'easy': return [1, 9];
        case 'medium': return [10, 99];
        case 'hard': return [100, 999];
        default: return [1, 9];
    }
}

export function generateTest(testId: TestType, difficulty: Difficulty): Question[] {
  const settings = getTestSettings(testId, difficulty);
  if (!settings) {
    return [];
  }

  const questions: Question[] = [];
  const [min, max] = getNumberRange(difficulty);

  for (let i = 0; i < settings.numQuestions; i++) {
    let questionText: string;
    let answer: number;

    switch (testId) {
      case 'addition-subtraction': {
        const numTerms = difficulty === 'easy' ? 2 : (difficulty === 'medium' ? 3 : 4);
        let numbers: number[] = [];
        let operations: ('+' | '-')[] = [];
        
        for (let j = 0; j < numTerms; j++) {
            numbers.push(getRandomInt(min, max));
        }

        let tempResult = numbers[0];
        let expression = `${numbers[0]}`;

        for (let j = 0; j < numTerms - 1; j++) {
            let op: '+' | '-' = getRandomInt(0, 1) === 0 ? '+' : '-';
            // Prevent negative intermediate results
            if (op === '-' && tempResult < numbers[j + 1]) {
                op = '+';
            }
            operations.push(op);
            expression += ` ${op} ${numbers[j + 1]}`;
            if (op === '+') {
                tempResult += numbers[j + 1];
            } else {
                tempResult -= numbers[j + 1];
            }
        }
        
        questionText = expression;
        answer = tempResult;
        break;
      }
      
      case 'multiplication':
        const m1_max = difficulty === 'easy' ? 9 : (difficulty === 'medium' ? 99 : 999);
        const m2_max = difficulty === 'hard' ? 99 : 9;
        const m1 = getRandomInt(min, m1_max);
        const m2 = getRandomInt(1, m2_max);
        answer = m1 * m2;
        questionText = `${m1} ร ${m2}`;
        break;

      case 'division':
        const divisor = getRandomInt(2, 9);
        const [answer_min, answer_max] = getNumberRange(difficulty);
        answer = getRandomInt(answer_min, answer_max);
        const dividend = divisor * answer;
        questionText = `${dividend} รท ${divisor}`;
        break;

      default:
        throw new Error('Invalid test type');
    }

    questions.push({
      text: questionText,
      options: generateOptions(answer),
      answer: answer,
    });
  }

  return questions;
}
